.App {
    margin: 0 auto;
}

.scorecontainer {
    height: 20vh;
}

.scoreboard {
    display: flex;
    height: 100%;
    border-bottom: 1px solid black;
}

.scorepanel {
    position: relative;
    flex: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}

.scorepanel .team {
    position: absolute;
    top: 0;
    left: 0;
}

.scorepanel .point {
    font-size: 3rem;
    font-weight: 600;
}

.scorepanel .foul {
    position: absolute;
    bottom: 0;
    right: 0;
}

.scorepanel .foul.teamfoul1 {
    color: orange;
    font-size: 1.5rem;
}
.scorepanel .foul.teamfoul2 {
    color: red;
    font-size: 1.5rem;
}

.clock {
    display: flex;
    flex: 3;
    flex-direction: column;
    align-items: center;
}

.clock button {
    flex: 1;
    margin: 0 auto;
    width: 100%;
    font-size: 2rem;
}

.teamcontainer {
    display: flex;
    height: 72vh;
    border-bottom: 1px solid black;
}

.teampanel {
    flex: 1;
    flex-direction: column;
}

.header {
    display: flex;
    height: 3vh;
    font-size: 0.8rem;
    border-bottom: 1px solid black;
    justify-items: center;
    align-items: center;
}
.header .info {
    flex: 1;
    display: flex;
}
.header .info .name {
    flex: 3;
    display: flex;
    margin-left: 10px;
}
.header .info .point {
    flex: 1;
    display: flex;
}
.header .info .foul {
    flex: 1;
    display: flex;
}

.playerpanel {
    display: flex;
    height: 25%;
    flex-direction: column;
}

.playerpanel .info {
    display: flex;
    flex: 1;
    align-items: center;
}
.playerpanel .info .name{
    flex: 3;
    margin-left: 10px;
}
.playerpanel .info .point{
    flex: 1;
}
.playerpanel .info .foul{
    flex: 1;
}

.playerpanel .button {
    display: flex;
    flex: 1;
}

.playerpanel button {
    font-size: 2rem;
}

.playerpanel .button .plus{
    flex: 1;
}
.playerpanel .button .minus{
    flex: 1;
}
.playerpanel .button .foul{
    flex: 1;
    font-size: 1rem;
}

.sharepanel {
    display: flex;
    color: white;
}
.sharepanel .button {
    margin-left: 10px;
    padding: 0 10px 0 10px;
}

.sharepanel .email {
    background: gray;
    border-radius: 5px;    
}

.sharepanel .kakaotalk {
    background: #ffe812;
    border-radius: 5px;    
}

.sharepanel .button.edit {
    padding: 0 10px 0 10px;
    background: gray;
    border-radius: 5px;
    margin-left: 30%;
}